import React from 'react';
import {Switch, Route} from 'react-router-dom';

import './App.css';

import HomePage from './pages/homepage/homepage.component';
import ShopPage from './pages/shop/shop.component';
import Header from './components/header/header.component'
import SignInAndSignUpPagefrom from './pages/sign-in-and-sign-up/sign-in-and-sign-up.component'
import { auth, createUserProfileDocument } from './firebase/firebase.utils';

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      currentUser: null
      //TODO check if it is ok to save url of pic on state
      //googleProfilePhoto: null
    }
  }

  unsubscribeFromAuth = null;

  componentDidMount(){
    this.unsubscribeFromAuth = auth.onAuthStateChanged(async userAuth => {
      if (userAuth){
        const userRef = createUserProfileDocument(userAuth);

        (await userRef).onSnapshot(snapShot => {
          this.setState({
              currentUser: {
                id : snapShot.id,
                ...snapShot.data()
              }
              //TODO check if it is ok to save url of pic on state
              //googleProfilePhoto : userAuth.photoURL
          });
        });
        
      }
      
      this.setState({currentUser: userAuth});
    });
  }

  componentWillUnmount(){
    this.unsubscribeFromAuth();
  }

  render(){
    return (
      <div>  
        <Header currentUser={this.state.currentUser}/>
        <Switch>
          <Route exact path='/' component = {HomePage}/>
          <Route path ='/shop' component = {ShopPage}/>
          <Route path ='/signin' component = {SignInAndSignUpPagefrom}/>
        </Switch>
      </div>
    )
  }
}

export default App;